<template>
	<Tabs v-model="tab" class="tab" @on-click="toggleTab">
		<Tab-pane label="待处理" name="pending" class="tab-item" :disabled="refreshFlag">
			<search-box @search="search" :refreshFlag="refreshFlag" :itemList="pendingTab.itemList"></search-box>
			<Table highlight-row size="large" :columns="pendingTab.pendingHeader" :data="list"></Table>
			<div class="page">
				<Page placement='top' :total="pendingTab.totalPage" :current="pendingTab.searchParams.currentPage" :page-size="pendingTab.searchParams.pageSize"
					  :page-size-opts="[10,20,30,40]"
					  show-elevator show-sizer show-total
					  @on-change="pageChange" @on-page-size-change="pageSizeChange"
				></Page>
			</div>
		</Tab-pane>
		<Tab-pane label="进行中" name="ongoing" class="tab-item" :disabled="refreshFlag">
			<search-box @search="search" :refreshFlag="refreshFlag" :itemList="ongoingTab.itemList"></search-box>
			<Table size="large" :columns="ongoingTab.ongoingHeader" :data="list"></Table>
			<div class="page">
				<Page placement='top' :total="ongoingTab.totalPage" :current="ongoingTab.searchParams.currentPage"
					  :page-size="ongoingTab.searchParams.pageSize" :page-size-opts="[10,20,30,40]"
					  show-elevator show-sizer show-total
					  @on-change="pageChange" @on-page-size-change="pageSizeChange"
				></Page>
			</div>
		</Tab-pane>
		<Tab-pane label="已完成" name="history" class="tab-item" :disabled="refreshFlag">
			<search-box @search="search" :refreshFlag="refreshFlag" :itemList="historyTab.itemList" stage="complete"></search-box>
			<Table size="large" :columns="historyTab.historyHeader" :data="list"></Table>
			<div class="page">
				<Page placement='top' :total="historyTab.totalPage" :current="historyTab.searchParams.currentPage"
					  :page-size="historyTab.searchParams.pageSize" :page-size-opts="[10,20,30,40]"
					  show-elevator show-sizer show-total
					  @on-change="pageChange" @on-page-size-change="pageSizeChange"
				></Page>
			</div>
		</Tab-pane>
		<i-button v-if="tab=='pending'" type="text" slot="extra" class="extra-btn" icon="ios-loop-strong" :disabled="refreshFlag" @click="loadNew">获取新进件</i-button>
		<i-button v-else type="text" slot="extra" class="extra-btn" icon="ios-loop-strong" :disabled="refreshFlag" @click="loadList">刷新</i-button>
	</Tabs>
</template>
<script>
	import PrintButton from '@/components/printbutton.vue'
	import searchBox from '@/components/searchbox.vue'
	export default {
		data () {
			return {
				pendingTab: {
					pendingHeader: [
						{
							type: 'index',
							title: '序号',
							align: 'center'
						},
						{
							title: '放款渠道',
							key: 'channelCode',
							align: 'center',
							render:(h,params)=>{
								let code=params.row.channelCode;
								let channelName='';
								switch (code) {
									case 1802:
										channelName = '外贸信托';
										break;
									case 1804:
										channelName = '新网银行';
										break;
									default:
										channelName = '未知渠道';
								}
								return h('span',channelName)
							}
						},
						{
							title: '贷款客户',
							key: 'name',
							align: 'center'
						},
						{
							title: '贷款产品',
							key: 'eProduct',
							align: 'center'
						},
						{
							title: '业务城市',
							key: 'city',
							align: 'center'
						},
						{
							title: '初审贷款金额',
							key: 'appMoney',
							align: 'center',
							render:(h,params)=>{
								return h('span',this.fmoney(params.row.appMoney))
							}
						},
						{
							title: '利率（年）',
							key: 'rate',
							align: 'center',
							render:(h,params)=>{
								return h('span',`${params.row.rate}%`)
							}
						},
						{
							title: '还款方式',
							key: 'repayWay',
							align: 'center'
						},
						{
							title: '进件时间',
							key: 'appDate',
							width:110,
							align: 'center',
							sortable: true,
							sortType:'desc',
							render: (h, params) => {
								return h('span',params.row.appDate ? new Date(params.row.appDate).Format("MM-dd hh:mm"):'-')
							}
						},
						{
							title:'所在环节',
							key:'nodeStatus',
							align:'center',
							render:(h,params)=>{
								let nodeStatus=params.row.nodeStatus;
								let nodeName='';
								switch (nodeStatus) {
									case 1001:
										nodeName = '授信初审';
										break;
									case 1002:
										nodeName = '授信复审';
										break;
									case 1003:
										nodeName = '放款初审';
										break;
									case 1004:
										nodeName = '放款复审';
										break;
									case 1005:
										nodeName = '放款终审';
										break;
									case 1006:
										nodeName = '账务确认';
										break;
									case 1080:
										nodeName = '进件';
										break;
									case 1081:
										nodeName = '放款资料传送';
										break;
									default:
										nodeName = '未知环节';
								}
								return h('span',nodeName)
							}
						},
						{
							title:'状态',
							key:'flowStatus',
							align:'center',
							render:(h,params)=>{
								let flowStatus=params.row.flowStatus;
								let nodeName='';
								switch (flowStatus) {
									case 3:
										nodeName = '补充资料';
										break;
									case -1:
										nodeName = '正在预审';
										break;
									default:
										nodeName = '未知状态';
								}
								return h('span',nodeName)
							}
						},
						{
							title: '操作',
							key: 'action',
							align: 'left',
							width: 180,
							render: (h, params) => {
								if (params.row.nodeStatus == 1081) {
									return h('div', [
										h('i-button', {
											props: {
												type: 'info',
												size: 'small',
											},
											style: {
												margin: '2px',
												paddingLeft:'15px',
												paddingRight:'15px'
											},
											on: {
												click: () => {
													this.$router.push({
														name: 'orderdetail',
														params: {
															loanId: params.row.loanId,
															serialNo:params.row.serialNo,
															nodeStatus:params.row.nodeStatus,
															tab: 'pending',
															name:this.$route.meta.activeName
														}
													})
												}
											}
										}, '处理'),
										h(PrintButton,{
											props:{
												id:params.row.loanId
											}
										}),
									])
								} else {
									return h('i-button', {
										props: {
											type: 'info',
											size: 'small',
										},
										style: {
											margin: '2px',
											paddingLeft:'15px',
											paddingRight:'15px'
										},
										on: {
											click: () => {
												this.$router.push({
													name: 'orderdetail',
													params: {
														loanId: params.row.loanId,
														serialNo:params.row.serialNo,
														nodeStatus:params.row.nodeStatus,
														tab: 'pending',
														name:this.$route.meta.activeName
													}
												})
											}
										}
									}, '处理')
								}
							}
						}
					],
					totalPage: 10,
					itemList:['aBaseName','channelCode','outReason'],
					searchParams:{
						flowStatus:10,
						currentPage:1,
						pageSize:10
					},
				},
				ongoingTab: {
					ongoingHeader: [
						{
							type: 'index',
							title: '序号',
							align: 'center'
						},
						{
							title: '放款渠道',
							key: 'channelCode',
							align: 'center',
							render:(h,params)=>{
								let code=params.row.channelCode;
								let channelName='';
								switch (code) {
									case 1802:
										channelName = '外贸信托';
										break;
									case 1804:
										channelName = '新网银行';
										break;
									default:
										channelName = '未知渠道';
								}
								return h('span',channelName)
							}
						},
						{
							title: '贷款客户',
							key: 'name',
							align: 'center'
						},
						{
							title: '贷款产品',
							key: 'eProduct',
							align: 'center'
						},
						{
							title: '业务城市',
							key: 'city',
							align: 'center'
						},
						{
							title: '初审贷款金额',
							key: 'appMoney',
							align: 'center',
							render:(h,params)=>{
								return h('span',this.fmoney(params.row.appMoney))
							}
						},
						{
							title: '利率（年）',
							key: 'rate',
							align: 'center',
							render:(h,params)=>{
								return h('span',`${params.row.rate}%`)
							}
						},
						{
							title: '还款方式',
							key: 'repayWay',
							align: 'center'
						},
						{
							title: '进件时间',
							key: 'appDate',
							width:110,
							align: 'center',
							sortable: true,
							sortType:'desc',
							render: (h, params) => {
								return h('span',params.row.appDate ? new Date(params.row.appDate).Format("MM-dd hh:mm"):'-')
							}
						},
						{
							title:'所在环节',
							key:'nodeStatus',
							align:'center',
							render:(h,params)=>{
								let nodeStatus=params.row.nodeStatus;
								let nodeName='';
								if(params.row.flowStatus == 1){//通过状态，节点码+1
									nodeStatus++
								}
								switch (nodeStatus) {
									case 1001:
										nodeName = '授信初审';
										break;
									case 1002:
										nodeName = '授信复审';
										break;
									case 1003:
										nodeName = '放款初审';
										break;
									case 1004:
										nodeName = '放款复审';
										break;
									case 1005:
										nodeName = '放款终审';
										break;
									case 1006:
										nodeName = '账务确认';
										break;
									case 1080:
										nodeName = '进件';
										break;
									case 1081:
										nodeName = '放款资料传送';
										break;
									default:
										nodeName = '未知环节';
								}
								return h('span',nodeName)
							}
						},
						{
							title:'状态',
							key:'flowStatus',
							align:'center',
							render:(h,params)=>{
								let flowStatus=params.row.flowStatus;
								let nodeName='正在审核';
								if(flowStatus == 0 || flowStatus == 1 ){
									if(params.row.nodeStatus == 1006){
										nodeName='正在处理'
									}else {
										nodeName='正在审核'
									}
								}else {
									nodeName='未知状态'
								}
								return h('span',nodeName)
							}
						},
						{
							title: '操作',
							key: 'action',
							width: 100,
							align: 'center',
							render: (h, params) => {
								return h('i-button', {
									props: {
										type: 'primary',
										size: 'small',
									},
									style: {
										marginRight: '5px'
									},
									on: {
										click: () => {
											this.$router.push({
												name: 'orderdetail',
												params: {
													loanId: params.row.loanId,
													serialNo:params.row.serialNo,
													nodeStatus:params.row.nodeStatus,
													tab: 'ongoing',
													name:this.$route.meta.activeName
												}
											})
										}
									}
								}, '查看')
							}
						}
					],
					totalPage: 10,
					itemList:['aBaseName','dateRange','channelCode'],
					searchParams: {
						flowStatus:11,
						currentPage: 1,
						pageSize: 10
					}
				},
				historyTab: {
					historyHeader: [
						{
							type: 'index',
							title: '序号',
							width: '80px',
							align: 'center'
						},
						{
							title: '放款渠道',
							key: 'channelCode',
							align: 'center',
							render:(h,params)=>{
								let code=params.row.channelCode;
								let channelName='';
								switch (code) {
									case 1802:
										channelName = '外贸信托';
										break;
									case 1804:
										channelName = '新网银行';
										break;
									default:
										channelName = '未知渠道';
								}
								return h('span',channelName)
							}
						},
						{
							title: '贷款客户',
							key: 'name',
							align: 'center'
						},
						{
							title: '贷款产品',
							key: 'eProduct',
							align: 'center'
						},
						{
							title: '业务城市',
							key: 'city',
							align: 'center'
						},
						{
							title: '初审贷款金额',
							key: 'appMoney',
							align: 'center',
							render:(h,params)=>{
								return h('span',this.fmoney(params.row.appMoney))
							}
						},
						{
							title: '利率（年）',
							key: 'rate',
							align: 'center',
							render:(h,params)=>{
								return h('span',`${params.row.rate}%`)
							}
						},
						{
							title: '还款方式',
							key: 'repayWay',
							align: 'center'
						},
						{
							title: '进件时间',
							key: 'appDate',
							width:110,
							align: 'center',
							sortable: true,
							sortType:'desc',
							render: (h, params) => {
								return h('span',params.row.appDate ? new Date(params.row.appDate).Format("MM-dd hh:mm"):'-')
							}
						},
						{
							title:'所在环节',
							key:'nodeStatus',
							align:'center',
							render:(h,params)=>{
								let nodeStatus=params.row.nodeStatus;
								let nodeName='';
								switch (nodeStatus) {
									case 1001:
										nodeName = '授信初审';
										break;
									case 1002:
										nodeName = '授信复审';
										break;
									case 1003:
										nodeName = '放款初审';
										break;
									case 1004:
										nodeName = '放款复审';
										break;
									case 1005:
										nodeName = '放款终审';
										break;
									case 1006:
										nodeName = '账务确认';
										break;
									case 1080:
										nodeName = '进件';
										break;
									case 1081:
										nodeName = '放款资料传送';
										break;
									default:
										nodeName = '未知环节';
								}
								return h('span',nodeName)
							}
						},
						{
							title:'状态',
							key:'flowStatus',
							align:'center',
							render:(h,params)=>{
								let flowStatus=params.row.flowStatus;
								let nodeName='未知';
								if(flowStatus == 1){//通过
									if(params.row.nodeStatus == 1006){
										nodeName='完成放款'
									}
								}else {
									nodeName='未通过'
								}
								return h('span',{
									style: {
										color: flowStatus == 1 ? '#19be6b' : '#ed3f14'
									}
								},nodeName)
							}
						},
						{
							title: '操作',
							key: 'action',
							align: 'left',
							width: 180,
							render: (h, params) => {
								return h('i-button', {
									props: {
										type: 'info',
										size: 'small',
									},
									style: {
										margin: '2px'
									},
									on: {
										click: () => {
											this.$router.push({
												name: 'orderdetail',
												params: {
													loanId: params.row.loanId,
													serialNo:params.row.serialNo,
													nodeStatus:params.row.nodeStatus,
													tab: 'history',
													name:this.$route.meta.activeName
												}
											})
										}
									}
								}, '查看详情')
							}
						}
					],
					totalPage: 10,
					itemList:['aBaseName','channelCode','status','dateRange'],
					searchParams: {
						flowStatus:12,
						currentPage: 1,
						pageSize: 10
					}
				},
				list:[],
				refreshFlag:false,
				nodeStatus:1080,
				tab: 'pending',
			}
		},
		components:{
			PrintButton,searchBox
		},
		created: function () {
			let tab = this.$route.params.tab;
			if (tab) {
				this.tab = tab;
			}
			this.loadList()
		},
		methods: {
			pageChange: function (val) {
				let _this = this;
				if (_this.tab == 'pending') {//待处理
					if (_this.pendingTab.searchParams.currentPage != val) {
						_this.pendingTab.searchParams.currentPage = val;
					}
				} else if (_this.tab == 'ongoing') {//进行中
					if (_this.ongoingTab.searchParams.currentPage != val) {
						_this.ongoingTab.searchParams.currentPage = val;
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.currentPage != val) {
						_this.historyTab.searchParams.currentPage = val;
					}
				};
				_this.loadList();
			},
			pageSizeChange: function (val) {
				let _this = this;
				if (_this.tab == 'pending') {//待处理
					if (_this.pendingTab.searchParams.pageSize != val) {
						_this.pendingTab.searchParams.pageSize = val;
					}
				} else if (_this.tab == 'ongoing') {//进行中
					if (_this.ongoingTab.searchParams.pageSize != val) {
						_this.ongoingTab.searchParams.pageSize = val;
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.pageSize != val) {
						_this.historyTab.searchParams.pageSize = val;
					}
				}
				_this.loadList();
			},
			toggleTab: function () {
				let _this=this;
				this.refreshFlag=true;
				clearTimeout(timer);
				let timer=setTimeout(function () {
					_this.loadList();
				},300)
			},
			loadList: function () {
				let _this=this;
				let searchParams;
				let url='loanManage/financialCommissioner';
				if (_this.tab == 'pending') {//待处理
					searchParams=_this.pendingTab.searchParams;
				} else if (_this.tab == 'ongoing') {//进行中
					searchParams=_this.ongoingTab.searchParams;
				} else if (_this.tab == 'history') {//历史记录
					searchParams=_this.historyTab.searchParams;
				}
				this.load(url,searchParams);
			},
			load: function (url,params) {
				let _this=this;
				_this.$Message.config({
					top: 200
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.refreshFlag=true;
				_this.$http.post(url,params)
					.then(function (response) {
						var res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.refreshFlag=false;
						if (res.code == 1) {//正常
							_this.$Message.success({
								content: res.msg
							});
							//设置页码
							_this.setPage({
								count:res.data.count,
								currentPage:res.data.currentPage,
								pageSize:res.data.pageSize
							});
							if(res.data.list){
								_this.list=res.data.list;
							}
						} else {
							_this.$Message.error(res.msg);
						}
					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.refreshFlag=false;
						_this.$Message.error({
							content: error
						});
					});
			},
			loadNew:function () {
				let searchParams=this.pendingTab.searchParams;
				let url='/ejie/checkNewOrder';
				let _this=this;
				_this.$Message.config({
					top: 200
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.refreshFlag=true;
				_this.$http.post(url,searchParams)
					.then(function (response) {
						var res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.refreshFlag=false;
						if (res.code == 1) {//正常
							_this.$Message.success({
								content: res.msg
							});
							_this.loadList();
						} else {
							_this.$Message.error(res.msg);
						}
					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.refreshFlag=false;
						_this.$Message.error({
							content: error
						});
					});
			},
			setPage:function (params) {
				if(this.tab == 'pending'){
					this.pendingTab.totalPage=params.count;
					this.pendingTab.searchParams.currentPage=params.currentPage;
					this.pendingTab.searchParams.pageSize=params.pageSize;
				}else if(this.tab == 'ongoing'){
					this.ongoingTab.totalPage=params.count;
					this.ongoingTab.searchParams.currentPage=params.currentPage;
					this.ongoingTab.searchParams.pageSize=params.pageSize;
				}else if(this.tab == 'history'){
					this.historyTab.totalPage=params.count;
					this.historyTab.searchParams.currentPage=params.currentPage;
					this.historyTab.searchParams.pageSize=params.pageSize;
				}else {
					this.$Message.error('出现了预料之外的情况,请重试！')
				}
			},
			search: function (searchParams) {
				let _this=this;
				if (_this.tab == 'pending') {//进行中
					_.assign(_this.pendingTab.searchParams,searchParams)
				}else if (_this.tab == 'ongoing') {//历史记录
					_.assign(_this.ongoingTab.searchParams,searchParams)
				} else if (_this.tab == 'history') {//历史记录
					_.assign(_this.historyTab.searchParams,searchParams)
				}
				_this.loadList();
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.page {
		padding-top: 50px;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		padding-bottom: 50px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

</style>



