<template>
	<i-button type="ghost" size="small" @click="getOrderInfo">打印合同</i-button>
</template>
<script>
	var CreatedOKLodop7766 = null;
	//====判断是否需要安装CLodop云打印服务器:====
	function needCLodop() {
		try {
			var ua = navigator.userAgent;
			if (ua.match(/Windows\sPhone/i) != null) return true;
			if (ua.match(/iPhone|iPod/i) != null) return true;
			if (ua.match(/Android/i) != null) return true;
			if (ua.match(/Edge\D?\d+/i) != null) return true;

			var verTrident = ua.match(/Trident\D?\d+/i);
			var verIE = ua.match(/MSIE\D?\d+/i);
			var verOPR = ua.match(/OPR\D?\d+/i);
			var verFF = ua.match(/Firefox\D?\d+/i);
			var x64 = ua.match(/x64/i);
			if ((verTrident == null) && (verIE == null) && (x64 !== null))
				return true; else if (verFF !== null) {
				verFF = verFF[0].match(/\d+/);
				if ((verFF[0] >= 42) || (x64 !== null)) return true;
			} else if (verOPR !== null) {
				verOPR = verOPR[0].match(/\d+/);
				if (verOPR[0] >= 32) return true;
			} else if ((verTrident == null) && (verIE == null)) {
				var verChrome = ua.match(/Chrome\D?\d+/i);
				if (verChrome !== null) {
					verChrome = verChrome[0].match(/\d+/);
					if (verChrome[0] >= 42) return true;
				}
				;
			}
			;
			return false;
		} catch (err) {
			return true;
		}
		;
	};
	//====页面引用CLodop云打印必须的JS文件：====
	if (needCLodop()) {
		var head = document.head || document.getElementsByTagName("head")[0] || document.documentElement;
		var oscript = document.createElement("script");
		oscript.src = "http://localhost:8000/CLodopfuncs.js?priority=1";
		head.insertBefore(oscript, head.firstChild);

		//引用双端口(8000和18000）避免其中某个被占用：
		oscript = document.createElement("script");
		oscript.src = "http://localhost:18000/CLodopfuncs.js?priority=0";
		head.insertBefore(oscript, head.firstChild);
	}
	;
	//====获取LODOP对象的主过程：====
	function getLodop(oOBJECT, oEMBED) {
		var strHtmInstall = "<br><font color='#FF00FF'>打印控件未安装!点击这里<a href='../plug/install_lodop32.exe' target='_self'>执行安装</a>,安装后请刷新页面或重新进入。</font>";
		var strHtmUpdate = "<br><font color='#FF00FF'>打印控件需要升级!点击这里<a href='../plug/install_lodop32.exe' target='_self'>执行升级</a>,升级后请重新进入。</font>";
		var strHtm64_Install = "<br><font color='#FF00FF'>打印控件未安装!点击这里<a href='../plug/install_lodop64.exe' target='_self'>执行安装</a>,安装后请刷新页面或重新进入。</font>";
		var strHtm64_Update = "<br><font color='#FF00FF'>打印控件需要升级!点击这里<a href='../plug/install_lodop64.exe' target='_self'>执行升级</a>,升级后请重新进入。</font>";
		var strHtmFireFox = "<br><br><font color='#FF00FF'>（注意：如曾安装过Lodop旧版附件npActiveXPLugin,请在【工具】->【附加组件】->【扩展】中先卸它）</font>";
		var strHtmChrome = "<br><br><font color='#FF00FF'>(如果此前正常，仅因浏览器升级或重安装而出问题，需重新执行以上安装）</font>";
		var strCLodopInstall = "<br><font color='#FF00FF'>CLodop云打印服务(localhost本地)未安装启动!点击这里<a href='../plug/CLodop_Setup_for_Win32NT.exe' target='_self'>执行安装</a>,安装后请刷新页面。</font>";
		var strCLodopUpdate = "<br><font color='#FF00FF'>CLodop云打印服务需升级!点击这里<a href='../plug/CLodop_Setup_for_Win32NT.exe' target='_self'>执行升级</a>,升级后请刷新页面。</font>";
		var LODOP;
		try {
			var isIE = (navigator.userAgent.indexOf('MSIE') >= 0) || (navigator.userAgent.indexOf('Trident') >= 0);
			if (needCLodop()) {
				try {
					LODOP = getCLodop();
				} catch (err) {
				}
				;
				if (!LODOP && document.readyState !== "complete") {
					alert("C-Lodop没准备好，请稍后再试！");
					return;
				}
				;
				if (!LODOP) {
					if (isIE) document.write(strCLodopInstall); else
						document.documentElement.innerHTML = strCLodopInstall + document.documentElement.innerHTML;
					return;
				} else {

					if (CLODOP.CVERSION < "2.1.6.3") {
						if (isIE) document.write(strCLodopUpdate); else
							document.documentElement.innerHTML = strCLodopUpdate + document.documentElement.innerHTML;
					}
					;
					if (oEMBED && oEMBED.parentNode) oEMBED.parentNode.removeChild(oEMBED);
					if (oOBJECT && oOBJECT.parentNode) oOBJECT.parentNode.removeChild(oOBJECT);
				}
				;
			} else {
				var is64IE = isIE && (navigator.userAgent.indexOf('x64') >= 0);
				//=====如果页面有Lodop就直接使用，没有则新建:==========
				if (oOBJECT != undefined || oEMBED != undefined) {
					if (isIE) LODOP = oOBJECT; else  LODOP = oEMBED;
				} else if (CreatedOKLodop7766 == null) {
					LODOP = document.createElement("object");
					LODOP.setAttribute("width", 0);
					LODOP.setAttribute("height", 0);
					LODOP.setAttribute("style", "position:absolute;left:0px;top:-100px;width:0px;height:0px;");
					if (isIE) LODOP.setAttribute("classid", "clsid:2105C259-1E0C-4534-8141-A753534CB4CA");
					else LODOP.setAttribute("type", "application/x-print-lodop");
					document.documentElement.appendChild(LODOP);
					CreatedOKLodop7766 = LODOP;
				} else LODOP = CreatedOKLodop7766;
				//=====Lodop插件未安装时提示下载地址:==========
				if ((LODOP == null) || (typeof(LODOP.VERSION) == "undefined")) {
					if (navigator.userAgent.indexOf('Chrome') >= 0)
						document.documentElement.innerHTML = strHtmChrome + document.documentElement.innerHTML;
					if (navigator.userAgent.indexOf('Firefox') >= 0)
						document.documentElement.innerHTML = strHtmFireFox + document.documentElement.innerHTML;
					if (is64IE) document.write(strHtm64_Install); else if (isIE) document.write(strHtmInstall); else
						document.documentElement.innerHTML = strHtmInstall + document.documentElement.innerHTML;
					return LODOP;
				}
				;
			}
			;
			if (LODOP.VERSION < "6.2.1.8") {
				if (!needCLodop()) {
					if (is64IE) document.write(strHtm64_Update); else if (isIE) document.write(strHtmUpdate); else
						document.documentElement.innerHTML = strHtmUpdate + document.documentElement.innerHTML;
				}
				;
				return LODOP;
			}
			;
			//===如下空白位置适合调用统一功能(如注册语句、语言选择等):===

			//===========================================================
			return LODOP;
		} catch (err) {
			alert("getLodop出错:" + err);
		}
		;
	};
	export default {
		data () {
			return {}
		},
		props: ['id'],
		methods: {
			getOrderInfo: function () {
				let _this = this;
				var LODOP = getLodop(document.getElementById('LODOP_OB'), document.getElementById('LODOP_EM'));
				_this.$Message.config({
					top: 200,
					duration: 2
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '合同数据请求中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('loanManage/warrantDetails', {
					loanId: _this.id
				})
					.then(function (response) {
						var res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						if (res.code == 1) {//正常
							_this.$Message.success({
								content: res.msg
							});
							_this.createPrintPage(res.data);
						} else {
							_this.$Message.error({
								content: res.msg
							});
							return false;
						}
					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Message.error({
							content: error
						});
						return false;
					});
			},
			createPrintPage: function (orderInfo) {
				let _this = this;
				let owner = _.size(orderInfo.commonApplicant);//共借人数量
				let poList = _.size(orderInfo.poList);//抵押人数量
				let pawn = _.size(orderInfo.mortgageHouse);//抵押物数量
				if (owner > 1) {//共借人数量大于一则手填
					this.$Notice.error({
						title: '共借人数量过多，请手动填写！'
					});
					return false
				} else if (poList > 2) {//抵押人数量大于二则手填
					this.$Notice.error({
						title: '抵押人数量过多，请手动填写！'
					});
					return false
				} else if (pawn > 5) {//抵押物数量大于5则手填
					this.$Notice.error({
						title: '抵押物过多，请手动填写！'
					});
					return false
				} else {
					//第一页
					//主借人
					let mainOwnerName = orderInfo.name;//姓名
					let mainOwnerNum = orderInfo.idNumber;//身份证号
					let mainOwnerAddress = orderInfo.permanentAddress;//住所地
					let mainOwnerPhoneNum = orderInfo.phoneNumber;//电话
					//共借人
					let cOwnerName = orderInfo.commonApplicant[0] ? orderInfo.commonApplicant[0].cAName : '/';//姓名
					let cOwnerCard = orderInfo.commonApplicant[0] ? '身份证' : '/';//证件类型
					let cOwnerNum = orderInfo.commonApplicant[0] ? orderInfo.commonApplicant[0].cAIdNumber : '/';//身份证号
					let cOwnerAdress = orderInfo.commonApplicant[0] ? '√' : '/';//同住所地
					let cOwnerPhoneNum = orderInfo.commonApplicant[0] ? orderInfo.commonApplicant[0].cAPhoneNumber : '/';//电话
					let cOwnerLiveAdress = orderInfo.commonApplicant[0] ? orderInfo.commonApplicant[0].cAResidenceAddress : '/';//住所地
					//抵押人1
					let poListAname = orderInfo.poList[0] ? orderInfo.poList[0].pName : '/';//姓名
					let poListAnum = orderInfo.poList[0] ? orderInfo.poList[0].pCardNum : '/';//身份证号码
					let poListAadress = orderInfo.poList[0] ? '√' : '/';//同住地
					let poListAphoneNum = orderInfo.poList[0] ? orderInfo.poList[0].pTelNum : '/';//电话
					let poListAliveAdress = orderInfo.poList[0] ? orderInfo.poList[0].pAddress : '/';//住所地
					//抵押人2
					let poListBname = orderInfo.poList[1] ? orderInfo.poList[1].pName : '/';//姓名
					let poListBnum = orderInfo.poList[1] ? orderInfo.poList[1].pCardNum : '/';//身份证号码
					let poListBadress = orderInfo.poList[1] ? '√' : '/';//同住地
					let poListBphoneNum = orderInfo.poList[1] ? orderInfo.poList[1].pTelNum : '/';//电话
					let poListBliveAdress = orderInfo.poList[1] ? orderInfo.poList[1].pAddress : '/';//住所地
					//抵押人3
					let poListCname = orderInfo.poList[2] ? orderInfo.poList[2].pName : '/';//姓名
					let poListCnum = orderInfo.poList[2] ? orderInfo.poList[2].pCardNum : '/';//身份证号码
					let poListCadress = orderInfo.poList[2] ? '√' : '/';//同住地
					let poListCphoneNum = orderInfo.poList[2] ? orderInfo.poList[2].pTelNum : '/';//电话
					let poListCliveAdress = orderInfo.poList[2] ? orderInfo.poList[2].pAddress : '/';//住所地
					//第二页
					let appMoney = orderInfo.appMoney;//借款金额
					let capitalization = orderInfo.capitalization;//借款金额大写
					let periods = orderInfo.periods;//借款期限
					let repayWay = orderInfo.repayWay;//还款方式
					let startDate = _this.initDefaultDate(0, 'd');//起始时间
					let endDate = _this.initDefaultDate(periods, 'M');//结束时间
					//根据借款期限判断贷款利率及利率调整方式
					let type = '/';//执行项
					let rate = '/';//固定利率
					let rateWay = '/';//浮动利率上浮/下浮
					let rateUp = '/';//浮动利率上浮/下浮比率
					let baseRate = '/';//基准利率
					let loanRate = '/';//贷款利率
					let rateUpWay = '/';//利率调整方式
					if (periods <= 12 && repayWay == '等额本息') {//期限小于等于12月，还款方式为等额本息
						type = '(1)';
						rate = '8.4';
					} else if (periods <= 12 && repayWay == '先息后本') {//期限小于等于12月，还款方式为先息后本
						type = '(1)';
						rate = '8.8';
					} else if (periods > 12) {//期限大于12月，浮动利率
						type = '(2)';
						rate = '/';
						rateWay = '上浮';
						rateUp = '77';
						baseRate = '4.75';
						loanRate = '8.4075';
					} else {
						_this.$Notice.error({
							title: '贷款利率及利率调整方式有误!',
							desc: '请联系管理员进行核对或使用人工填写！'
						});
						return false;
					}
					;
					//第三页
					if (repayWay != '等额本息' && repayWay != '先息后本') {
						_this.$Notice.error({
							title: '还款方式错误!',
							desc: '请联系管理员进行核对或使用人工填写！'
						});
						return false;
					}
					;
					let bank = `${orderInfo.cCardBank}${orderInfo.branchbank}`;//放款卡 银行+支行
					let bankName = orderInfo.cCardName;//放款卡 持卡人姓名
					let bankNum = orderInfo.cCardNumber;//放款卡卡号

					if (!orderInfo.repayCardBank || !orderInfo.repayCardBranck || !orderInfo.repayCardUser || !orderInfo.repayCardNo) {//判断还款卡是否存在
					 _this.$Notice.error({
					 title: '还款卡错误!',
					 desc: '请联系管理员进行核对或使用人工填写！'
					 });
					 return false;
					 };
					let repayBank = `${orderInfo.repayCardBank}${orderInfo.repayCardBranck}`;//还款卡 银行+支行
					let repayBankName = orderInfo.repayCardUser;//还款卡 持卡人姓名
					let repayBankNum = orderInfo.repayCardNo;//还款卡卡号

					let loanPurpose = orderInfo.loanPurpose;//贷款用途
					if (loanPurpose != '综合消费' && loanPurpose != '生产经营') {//贷款用途判断
						_this.$Notice.error({
							title: '贷款用途错误!',
							desc: '请联系管理员进行核对或使用人工填写！'
						});
						return false;
					};

					//第六页
					let house = orderInfo.mortgageHouse;//抵押物列表,当前只有房
					//打印开始
					LODOP.PRINT_INITA(0, 0, 793, 1096, "新网合同打印");
					LODOP.SET_PRINT_PAGESIZE(1, 2100, 2900, "新网合同");
					//第一页
					LODOP.ADD_PRINT_TEXT(270, 190, 100, 20, mainOwnerName);//主借人
					LODOP.ADD_PRINT_TEXT(295, 190, 85, 20, "身份证");//主借人证件类型
					LODOP.ADD_PRINT_TEXT(295, 345, 150, 20, mainOwnerNum);//主借人身份证号
					LODOP.ADD_PRINT_TEXT(340, 195, 20, 20, "√");//同住所地
					LODOP.ADD_PRINT_TEXT(338, 365, 300, 20, "");
					LODOP.ADD_PRINT_TEXT(340, 280, 20, 20, "");
					LODOP.ADD_PRINT_TEXT(410, 190, 100, 20, cOwnerName);//共借人
					LODOP.ADD_PRINT_TEXT(432, 190, 80, 20, cOwnerCard);//共借人证件类型
					LODOP.ADD_PRINT_TEXT(432, 345, 140, 20, cOwnerNum);//共借人身份证号
					LODOP.ADD_PRINT_TEXT(477, 195, 20, 20, cOwnerAdress);//共借人-同住所地
					LODOP.ADD_PRINT_TEXT(477, 280, 20, 20, orderInfo.commonApplicant[0] ? '' : '/');//没有则'/'划掉
					LODOP.ADD_PRINT_TEXT(477, 365, 300, 20, orderInfo.commonApplicant[0] ? '' : '/');//没有则'/'划掉
					LODOP.ADD_PRINT_TEXT(182, 205, 150, 20, "王航");
					LODOP.ADD_PRINT_TEXT(570, 190, 100, 20, poListAname);//抵押人1
					LODOP.ADD_PRINT_TEXT(592, 190, 80, 20, orderInfo.poList[0] ? '身份证' : '/');//抵押人1证件类型
					LODOP.ADD_PRINT_TEXT(592, 345, 150, 20, poListAnum);//抵押人1身份证号码
					LODOP.ADD_PRINT_TEXT(637, 195, 20, 20, poListAadress);//抵押人1-同住所地
					LODOP.ADD_PRINT_TEXT(637, 280, 20, 20, orderInfo.poList[0] ? '' : '/');//没有则'/'划掉
					LODOP.ADD_PRINT_TEXT(637, 365, 300, 20, orderInfo.poList[0] ? '' : '/');//没有则'/'划掉
					LODOP.ADD_PRINT_TEXT(707, 190, 100, 20, poListBname);//抵押人2
					LODOP.ADD_PRINT_TEXT(730, 190, 80, 20, orderInfo.poList[1] ? '身份证' : '/');//抵押人2证件类型
					LODOP.ADD_PRINT_TEXT(730, 345, 150, 20, poListBnum);//抵押人2身份证号码
					LODOP.ADD_PRINT_TEXT(775, 195, 20, 20, poListBadress);//抵押人2-同住所地
					LODOP.ADD_PRINT_TEXT(775, 280, 20, 20, orderInfo.poList[1] ? '' : '/');
					LODOP.ADD_PRINT_TEXT(775, 365, 300, 20, orderInfo.poList[1] ? '' : '/');
					LODOP.ADD_PRINT_TEXT(843, 190, 100, 20, poListCname);//抵押人3
					LODOP.ADD_PRINT_TEXT(867, 190, 80, 20, orderInfo.poList[2] ? '身份证' : '/');//抵押人3-证件类型
					LODOP.ADD_PRINT_TEXT(867, 345, 150, 20, poListCnum);//抵押人3身份证号码
					LODOP.ADD_PRINT_TEXT(911, 195, 20, 20, poListCadress);//抵押人3同住所地
					LODOP.ADD_PRINT_TEXT(911, 280, 20, 20, orderInfo.poList[2] ? '' : '/');//抵押人3
					LODOP.ADD_PRINT_TEXT(911, 365, 300, 20, orderInfo.poList[2] ? '' : '/');//抵押人3
					LODOP.ADD_PRINT_TEXT(365, 190, 150, 20, mainOwnerPhoneNum);//主借人电话
					LODOP.ADD_PRINT_TEXT(501, 190, 150, 20, cOwnerPhoneNum);//共借人电话
					LODOP.ADD_PRINT_TEXT(661, 190, 150, 20, poListAphoneNum);//抵押人1电话
					LODOP.ADD_PRINT_TEXT(798, 190, 150, 20, poListBphoneNum);//抵押人2电话
					LODOP.ADD_PRINT_TEXT(936, 190, 150, 20, poListCphoneNum);//抵押人3电话
					LODOP.ADD_PRINT_TEXT(318, 190, 300, 20, mainOwnerAddress);//主借人地址
					LODOP.ADD_PRINT_TEXT(455, 190, 300, 20, cOwnerLiveAdress);//共借人地址
					LODOP.ADD_PRINT_TEXT(615, 190, 300, 20, poListAliveAdress);//抵押人1地址
					LODOP.ADD_PRINT_TEXT(753, 190, 300, 20, poListBliveAdress);//抵押人2地址
					LODOP.ADD_PRINT_TEXT(890, 190, 300, 20, poListCliveAdress);//抵押人3地址
					//第二页
					LODOP.NewPageA();
					LODOP.ADD_PRINT_TEXT(150, 203, 100, 20, appMoney);//借款金额
					LODOP.ADD_PRINT_TEXT(150, 405, 265, 20, capitalization);//大写借款金额
					LODOP.ADD_PRINT_TEXT(220, 245, 50, 20, `${periods}个月`);//借款期限
					LODOP.ADD_PRINT_TEXT(220, 335, 130, 20, startDate);//开始日期
					LODOP.ADD_PRINT_TEXT(220, 490, 120, 20, endDate);//结束日期
					LODOP.ADD_PRINT_TEXT(345, 465, 28, 20, type);//利率执行项
					LODOP.ADD_PRINT_TEXT(373, 282, 30, 20, rate);//固定利率
					LODOP.ADD_PRINT_TEXT(398, 605, 35, 20, rateWay);//浮动利率方式，上浮/下浮
					LODOP.ADD_PRINT_TEXT(425, 345, 30, 20, rateUp);//上浮比例
					LODOP.ADD_PRINT_TEXT(450, 350, 50, 20, baseRate);//基准利率
					LODOP.ADD_PRINT_TEXT(450, 548, 50, 20, loanRate);//签订时贷款利率
					LODOP.ADD_PRINT_TEXT(477, 495, 28, 20, rateUpWay);//浮动利率调整方式
					LODOP.ADD_PRINT_TEXT(555, 308, 21, 20, "/");//划掉按年调整时间
					LODOP.ADD_PRINT_TEXT(555, 338, 21, 20, "/");//划掉按年调整时间
					LODOP.ADD_PRINT_TEXT(835, 360, 28, 20, "(1)");//还款日执行项
					LODOP.ADD_PRINT_TEXT(865, 333, 22, 20, "26");//约定还款日
					LODOP.ADD_PRINT_TEXT(916, 265, 420, 20, "/");//其他划掉
					//第三页
					LODOP.NewPageA();
					LODOP.ADD_PRINT_TEXT(85, 263, 20, 20, repayWay == '等额本息' ? '√' : '');//等额本息
					LODOP.ADD_PRINT_TEXT(85, 405, 20, 20, repayWay == '先息后本' ? '√' : '');//先息后本
